import React, { Component } from "react";
import List from "./list";
import Head from "next/head";

class App extends Component {
  state = {
    term: "",
    items: []
  };
  onChange = action => {
    this.setState({ term: action.target.value });
  };

  onSubmit = action => {
    action.preventDefault();
    if (this.state.term == "") {
      alert("Please enter a todo item");
    }
    this.setState({
      term: "",
      items: [...this.state.items, this.state.term]
    });
  };
  onDelete = action => {
    const items = this.state.items.filter(c => c !== action);
    this.setState({ items });
  };

  render() {
    return (
      <div className="container">
        <Head>
          <title>Todo-list App</title>
          <link
            rel="stylesheet"
            href="https://bootswatch.com/4/cerulean/bootstrap.min.css"
          />
          <link
            href="https://fonts.googleapis.com/css?family=Margarine|Germania One"
            rel="stylesheet"
          />
        </Head>
        <p
          style={{ color: "green", "font-family": "Germania One" }}
          className="h1"
        >
          What will you like to do?
        </p>
        <div className="form-group">
          <form onSubmit={this.onSubmit}>
            <input value={this.state.term} onChange={this.onChange} />

            <button className="btn btn-primary m-2">Submit</button>
          </form>
        </div>

        <List items={this.state.items} onDelete={this.onDelete} />
      </div>
    );
  }
}

export default App;
